<template>
  <div id="calendar-entry">
    <div class="calendar-entry-note">
      <input type="text" placeholder="New Event"/>
      <p class="calendar-entry-day">Day of event: <span class="bold">{{activeDayTitle}}</span></p>
      <a class="button is-primary is-small is-outlined">Submit</a>
    </div>
  </div>
</template>

<script>
  import {store} from '../store';

  export default {
    name: "CalendarEntry",
    computed: {
      activeDayTitle() {
        return store.getActiveDay().title
      }
    }
  }
</script>

<style lang="scss" scoped>
  #calendar-entry {
    clear: both;
    background: #FFF;
    border: 1px solid #42b883;
    border-radius: 10px;
    max-width: 300px;
    margin: 0 auto;
    padding: 20px;

    .calendar-entry-note {
      input {
        width: 200px;
        font-weight: 600;
        border: 0;
        border-bottom: 1px solid #CCC;
        font-size: 15px;
        height: 30px;
        margin-bottom: 10px;

        &:focus {
          outline: none;
        }
      }

      .submit {
        display: block;
        margin: 0 auto;
      }
    }
  }
</style>



